
let el = (tag) => {
  let element = null;
  let parent = null;
  
  function __el(selector){
      if(!selector){
          return __el;
      }

      // TODO
      // check if element selector is valid

      let tag = getTag(selector);
      let class_names = getClassNames(selector);
      let id = getIds(selector);

      element = document.createElement(tag);

      if(class_names.length > 0){
        element.classList.add(...class_names);
      }

      if(id){
        element.id = id;
      }

      return __el;
  }

  /**
   * Adds an event handler for the given event
   * @param {} event_name dom event name
   * @param {*} handler callback for event
   */
  __el.on = (event_name, handler) => {
      // TODO
      // check if event is valid
      // TODO
      // add support for event handler options
      element.addEventListener(event_name, handler);

      return __el;
  }

  /**
   * Adds the given attribute to the current html
   * element
   * @param {string} attr html attribute name to set
   * @param {string} value value for the given attribute
   */
  __el.attr = (attr, value) => {
      if(!value){
          if(attr in element){
              return element[attr];
          }
          console.warn(`attribute ${attr} not found in element ${element}`);
          return null;
      }

      // TODO
      // validate attribute and value
      setProps(element, {[attr]: value});

      return __el;
  };

  /**
   * Applies multiple attributes provided as key value pairs to this current object
   * @param {object} attributes key-value pairs of attribute names and values
   *                            to be applied to this element
   */
  __el.attrs = (attributes) => {
      setProps(element, attributes);
      return __el;
  }

  /**
   * Returns the dom element stored in this object
   */
  __el.dom = () => element;

  /**
   *  Renders this element to the given target element
   * @param {element} target_div the dom node to which this element
   *                             is rendered into
   */
  __el.render = (target_div) => {
      target_div.append(element);

      return __el;
  }

  __el.delete = () => {
    if(!element || !element.parentElement){
      console.warn("Trying to delete an element that doesn't have a parent");
      return;
    }

    element.parentElement.removeChild(element);
  }

  __el.classed = (className, hasClass) => {
    if(hasClass){
      element.classList.add(className);
    }else{
      element.classList.remove(className);
    }
  }

  __el.append = (...args) => {
    if(!element){
      return __el;
    }
    
    // check each element in the argument
    // list is an array, a dom element or
    // dom-helper el
    args.forEach((entry) => {
      if(Array.isArray(entry)){
        entry.forEach(array_element => {
          __el.append(array_element);
        });
      }else if(entry instanceof Element){
        element.appendChild(entry);
      }else if(entry.hasOwnProperty("dom")){
        // TODO: more robust checking to see
        // if entry is of type __el
        element.appendChild(entry.dom());
      }
    });

    return __el;
  };

  return __el(tag);
}


const setProps = (element, attributes) => {
  // set basic attributes
  for(let attribute in attributes){
      if(attribute in element){
          element[attribute] = attributes[attribute];
      }else{
          console.warn(`trying to set property ${attribute} on element ${element}`);
      }
  }
};

const isDomElement = (e) => e instanceof Element;

/**
* Regex helpers
*/
const getIds = (expr) => {
  const ELEMENT_ID_PATTERN = /#([^\.|^#]+)/;
  let matches = ELEMENT_ID_PATTERN.exec(expr);
  if(!matches){
    return null;
  }else{
    return matches[1];
  }
};

const getClassNames = (expr) => {
  const ELEMENT_CLASS_PATTERN=/\.([^\.|^#]+)/g;
  let classNames = [];
  let match = ELEMENT_CLASS_PATTERN.exec(expr);
  
  while(match){
      classNames.push(match[1]);
      match = ELEMENT_CLASS_PATTERN.exec(expr);
  }

  return classNames;
};

const getTag = (expr) => {
  const ELEMENT_TAG_PATTERN = /^([a-zA-Z]+)/;
  let matches = ELEMENT_TAG_PATTERN.exec(expr);
  if(!matches){
    return null;
  }else{
    return matches[1];
  }
};

export {el};